import React, { useRef, useState } from 'react';
import useSound from 'use-sound';
import gsap from 'gsap';
import { Physics2DPlugin } from './plugins/Physics2DPlugin'; //     


// GSAP Physics2D  
gsap.registerPlugin(Physics2DPlugin);

export default function PushBtn() {
    const [play] = useSound(`${process.env.PUBLIC_URL}/coin.wav`); //   
    const containerRef = useRef(null); //   
    const [messageCount, setMessageCount] = useState(0); //  ID 

  //   
    const randomMessages = [
        ' ! 2025  ',
        '   !',
        '   2025 !',
        '    !',
        '   !',
        '    !  ~',
        ' !     !',
        '     ',
        ' 2025,      !',
        ' 2025,   ',
    ];

  //   
    const handleClick = () => {
        play(); //  

        document.body.style.overflow = 'hidden';

        const randomMessage =
        randomMessages[Math.floor(Math.random() * randomMessages.length)];

        const newMessageId = `message-${messageCount}`;
        setMessageCount((count) => count + 1);

        //   
        const newBox = document.createElement('div');
        newBox.className = 'box';
        newBox.textContent = randomMessage;
        newBox.style.position = 'absolute';
        newBox.style.top = '55%';
        newBox.style.left = '50%';
        newBox.style.transform = 'translate(-50%, -50%)';
        containerRef.current.appendChild(newBox);

        // GSAP Physics2D  
        gsap.to(newBox, {
            physics2D: {
                velocity: Math.random() * 300 + 200, //   (200~500px/s)
                angle: Math.random() * 360, //   (0~360)
                gravity: 400, // 
            },
            duration: 3, //   
            onComplete: () => {
                newBox.remove(); //     
            },
            });
        };

    return (
        <div className="push-main">
        <div className="line-caption">
            <div className="flowing-text-container">
            {/*        */}
            {[...Array(5)].map((_, index) => (
                <div key={index} className="flowing-text">
                Push the Luck Button and make a Wish! Push the Luck Button and make a wish! Push the Luck Button and make a wish!
                </div>
            ))}
            </div>
        </div>
        <div className="push-title">
            <img src={`${process.env.PUBLIC_URL}/title-text.svg`} alt="Title Text" />
        </div>  
        {/*   */}
        <div ref={containerRef} className="message-container"></div>
        {/*        */}
        <button className="push-button" onClick={handleClick}>
            <span className="back"></span>
            <span className="front"></span>
        </button>
        </div>
    );
}